import React, {useState } from "react";
import Loading from "../Loading";
import { FeturedProducts, useProduct } from "../../useProduct";
import { Product } from "../Product";
import { TextField } from "@mui/material";
import { Helmet } from "react-helmet";

export default function Products() {
  let { data, error, isError, isLoading } = useProduct('products',FeturedProducts)
  const [searcheditems,setsearcheditems] = useState([])


  function SearchForProduct(e){
    const search= e.target.value;
    const Arr =data?.filter((ele)=>ele.title.toLowerCase().trim().includes(search.toLowerCase().trim()))
    setsearcheditems(Arr)
    }

  return (<>
<Helmet>
  <title>Products</title>
  <meta name="description" content="Helmet application" />
</Helmet>

  <TextField id="outlined-basic" label="Search" variant="outlined" className='form-control my-5' onChange={SearchForProduct} />

    <div className="row gy-4">
      {isLoading ? (
        <Loading></Loading>
      ) : isError ? 
        <Error key={error} error={error}></Error>
        : 
        searcheditems.length?searcheditems.map((prod) => <Product key={prod._id} prod={prod}></Product>):
          data?.map((prod) => <Product key={prod._id} prod={prod}></Product>)
      }
    </div>
      </>
  );
}

export function Error({error}) {
  return ( <h2 className="d-flex justify-content-center align-items-center">{error.message}</h2>);
}

