import React, { useContext, useState } from "react";
import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import logo from '../../assets/freshcart-logo.svg'
import { Link, useNavigate } from "react-router-dom";
import {  NavbarBrand } from "react-bootstrap";
import { Usercontext } from "../../UserContext";
import Modal from 'react-bootstrap/Modal';
import { useCardtoGet, usegetCard } from "../UseCart";


export default function NavBar() {
  const navigate =useNavigate()
  const {User,setUser,setOpen,setLogin,Login}= useContext(Usercontext)
  let {data} = useCardtoGet('Getcard',usegetCard)


  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  function Logout() {
    setUser(null);
    setLogin(null);
    localStorage.removeItem('UserToken');
    localStorage.removeItem('UserName');
    navigate("/")
  }
  return (
    <>
        <Navbar expand="lg" className="bg-body-tertiary">
          <Container>
            <NavbarBrand as={Link} to="home"><img src={logo} alt="" /></NavbarBrand>
            <Navbar.Toggle aria-controls="basic-navbar-nav" />
            <Navbar.Collapse id="basic-navbar-nav">
              {User?
              <Nav className="me-auto">
                <Nav.Link as={Link} to="home">Home</Nav.Link>
                <Nav.Link as={Link} to="Wishlist">Wishlist</Nav.Link>
                <Nav.Link as={Link} to="products">Product</Nav.Link>
                <Nav.Link as={Link} to="categories">Categories</Nav.Link>
                <Nav.Link as={Link} to="brands">Brands</Nav.Link>
              </Nav>:""
              }
              <Nav className="ms-auto">
                <div className="d-flex  gap-1">
                <Nav.Link as={Link} to=""><i className=" fa-brands fa-instagram"></i></Nav.Link>
                <Nav.Link as={Link} to=""><i className=" fa-brands fa-facebook-f"></i></Nav.Link>
                <Nav.Link as={Link} to=""><i className=" fa-brands fa-tiktok"></i></Nav.Link>
                <Nav.Link as={Link} to=""><i className=" fa-brands fa-twitter"></i></Nav.Link>
                <Nav.Link as={Link} to=""><i className=" fa-brands fa-linkedin-in"></i></Nav.Link>
                <Nav.Link as={Link} to=""><i className=" fa-brands fa-youtube"></i></Nav.Link>
                </div>
                {User?<Navbar.Text className="cursorPointer px-3"  onClick={Logout}>Logout</Navbar.Text>:<>
                <Nav.Link as={Link} to="Login">Login</Nav.Link>
                <Nav.Link as={Link} to="register">Register</Nav.Link>
                </>
                }
                <Navbar.Text className="position-relative mx-3" as={Link} to="Cart" onClick={User?  ()=>{setOpen(true)}:handleShow}>
                <i className="fa-solid fa-cart-shopping" ></i>
                <span className='d-inline-block cart d-flex justify-content-center align-items-center position-absolute regbtn rounded-circle '>{data?.numOfCartItems?data?.numOfCartItems:'0'}</span>
                  </Navbar.Text> 
                {Login?<Navbar.Text className="d-flex mx-4 gap-2">
                <span className="fw-bolder">HI</span>
                <span>{Login}</span>
                  </Navbar.Text> :""}
                
              </Nav>
            </Navbar.Collapse>
          </Container>
        </Navbar>

    <>


      <Modal show={show} onHide={handleClose} style={{top:'30%'}}>
        <Modal.Body>Please Login To Use Cart</Modal.Body>
      </Modal>
    </>
    </>
  );
}
