import axios from 'axios';
import { useFormik } from 'formik'
import React, { useContext, useState } from 'react'
import { Bars } from 'react-loader-spinner';
import { Link, useNavigate } from 'react-router-dom';
import * as Yup from 'yup';
import { Usercontext } from '../../UserContext';

export default function RessetPass() {
  const Navigate =useNavigate()
  const [laod,setlaoding]=useState(false);
  const [msg,setMsg]=useState('');
  const {setUser,setLogin}= useContext(Usercontext)

  async function Reset(values){
    try {
      setlaoding(true)
      const {data} = await axios.put('https://ecommerce.routemisr.com/api/v1/auth/resetPassword',values);
        setUser(data.token)
        localStorage.setItem('UserToken',data.token)
        localStorage.removeItem('Code')
        Navigate("/")
        setMsg("")
        setlaoding(false)

    } catch (error) {
      setlaoding(true)
      console.log(error);
      setMsg(error.response.data.message)
      setlaoding(false)
    }
  }
  const validationSchema = Yup.object({
    email:Yup.string().required("Email Is Required").email("Enter Valid EMail"),
    newPassword:Yup.string().required("Password Is Required").matches(/^[A-Z][a-z0-9]{5,10}$/,"Invalid Password"),
  })

  
  const formik=useFormik({
      initialValues:{
        email:'',
        newPassword:'',
      },
      validationSchema,
      onSubmit:Reset
    })


    
  return (<>
    <div>
      <form className='w-75 mx-auto' onSubmit={formik.handleSubmit}>
        <h3 className='pb-4'>reset your account password</h3>

        <label htmlFor="email">email:</label>
        <input type="email" id='email' className='form-control mb-3' value={formik.values.email} onChange={formik.handleChange} onBlur={formik.handleBlur}/>
        {formik.errors.email && formik.touched.email?<p className='alert alert-danger'>{formik.errors.email} </p>:"" }


        <label htmlFor="newPassword">newPassword:</label>
        <input type="password" id='newPassword' className='form-control mb-3' value={formik.values.newPassword} onChange={formik.handleChange} onBlur={formik.handleBlur}/>
        {formik.errors.newPassword && formik.touched.newPassword?<p className='alert alert-danger'>{formik.errors.newPassword} </p>:"" }

        {msg? <p className=' alert alert-danger'>{msg}</p> :""}

        <div className='d-flex justify-content-between'>

        <button disabled={!(formik.dirty&& formik.isValid)} type='submit' className='btn  regbtn text-white ms-auto d-block' >{laod?<Bars
  height="24"
  width="65"
  color="white"
  ariaLabel="bars-loading"
  wrapperStyle={{}}
  wrapperClass=""
  visible={true}
  />:"Reset Password"}</button>
  </div>
      </form>
    </div>
  </>
  )
}
