import axios from 'axios';
import { useFormik } from 'formik'
import React, { useContext, useState } from 'react'
import { Bars } from 'react-loader-spinner';
import { Link, useNavigate } from 'react-router-dom';
import * as Yup from 'yup';
import { Usercontext } from '../../UserContext';
import { Helmet } from "react-helmet";

export default function Register() {
  const Navigate =useNavigate()
  const [laod,setlaoding]=useState(false);
  const [msg,setMsg]=useState('');
  const {setUser,setLogin}= useContext(Usercontext)

  async function getLogin(values){
    try {
      setlaoding(true)
      const {data} = await axios.post('https://ecommerce.routemisr.com/api/v1/auth/signin',values);
      if (data.message === "success"){
        setUser(data.token)
        setLogin(data.user.name)
        localStorage.setItem('UserToken',data.token)
        localStorage.setItem('UserName',data.user.name)
        Navigate("/home")
        setMsg("")
        setlaoding(false)
      }

    } catch (error) {
      setlaoding(true)
      setMsg(error.response.data.message)
      setlaoding(false)
    }
  }
  const validationSchema = Yup.object({
    email:Yup.string().required("Email Is Required").email("Enter Valid EMail"),
    password:Yup.string().required("Password Is Required").matches(/^[A-Z][a-z0-9]{5,10}$/,"Invalid Password"),
  })

  
  const formik=useFormik({
      initialValues:{
        email:'',
        password:'',
      },
      validationSchema,
      onSubmit:getLogin
    })


    
  return (<>
  <Helmet>
  <title>Login</title>
  <meta name="description" content="Helmet application" />
</Helmet>
    <div>
      <form className='w-75 mx-auto' onSubmit={formik.handleSubmit}>
        <h3 className='pb-4'>Login:</h3>

        <label htmlFor="email">email:</label>
        <input type="email" id='email' className='form-control mb-3' value={formik.values.email} onChange={formik.handleChange} onBlur={formik.handleBlur}/>
        {formik.errors.email && formik.touched.email?<p className='alert alert-danger'>{formik.errors.email} </p>:"" }


        <label htmlFor="password">password:</label>
        <input type="password" id='password' className='form-control mb-3' value={formik.values.password} onChange={formik.handleChange} onBlur={formik.handleBlur}/>
        {formik.errors.password && formik.touched.password?<p className='alert alert-danger'>{formik.errors.password} </p>:"" }

        {msg? <p className=' alert alert-danger'>{msg}</p> :""}
        <div className='d-flex justify-content-between'>


        <Link to='/Forgetpass' className='link'>forget your password ?</Link>
        <button disabled={!(formik.dirty&& formik.isValid)} type='submit' className='btn  regbtn text-white ms-auto d-block' >{laod?<Bars
  height="24"
  width="65"
  color="white"
  ariaLabel="bars-loading"
  wrapperStyle={{}}
  wrapperClass=""
  visible={true}
  />:"Login"}</button>
  </div>
      </form>
    </div>
  </>
  )
}
