import axios from 'axios'
import React from 'react'
import { useQuery } from 'react-query'
import Loading from '../Loading'
import { Error } from '../Products/Products'
import { Helmet } from 'react-helmet'

export default function Brands() {
  function GetBrandsApi(){
    return axios.get('https://ecommerce.routemisr.com/api/v1/brands')
  }
  
      let {data ,isLoading,isError,error,refetch}= useQuery('Brands',GetBrandsApi,{
        select:(data)=>data?.data?.data,
        enabled:false
      })
      console.log(data);
    
  return (<>
  <Helmet>
  <title>Brands</title>
  <meta name="description" content="Helmet application" />
</Helmet>
    {isLoading ?
   (<Loading></Loading>) :
  isError ? 
  <Error key={error} error={error}></Error>: <>
    <h2 className='cursor-pointer text-main text-center h1 fw-bolder catCard m-3' onClick={refetch}>All Brands</h2>
    <div className='row g-5'>
      {data?.map((ele)=><div className="col-md-3 catCard" key={ele._id}>
          <img src={ele.image} alt="" className='w-100'/>
          <h3 className='bg-success-subtle text-center '>{ele.name}</h3>
      </div>
      )}
    </div>
      </>}
      </>
)
}
