import React, { useContext } from 'react'
import { useParams } from 'react-router-dom'
import Loading from '../Loading'
import { FeturedProductsDetails, useProduct } from '../../useProduct'
import { useCart, useCartMutation } from "../UseCart";
import Slider from 'react-slick';
import { Helmet } from 'react-helmet';
import { Usercontext } from '../../UserContext';

export default function ProductDetails() {
  const {Login}= useContext(Usercontext)
    let {id} =useParams()
    let { data, error, isError, isLoading } = useProduct('productsDetails',()=>FeturedProductsDetails(id))
    let { mutate } = useCartMutation(useCart)
    var settings = {
      dots: true,
      infinite: true,
      speed: 500,
      slidesToShow: 1,
      slidesToScroll: 1,
      autoplay:true
    };
 
    
  return (<>
   
      {isLoading ? (
          <Loading></Loading>
          ) : isError ? 
          <Error key={error} error={error}></Error>
          : 
          <div className='row align-items-center'>
        <div className="col-md-4 ">
        <Slider {...settings}>
    {data?.images.map((ele)=> 
        <img key={ele} src={ele} alt=""  className='w-100' />
    )}
    </Slider>
        </div>
        <div className="col-md-8">
            <h2>{data.title}</h2>
            <p className='p-3'>{data.description}</p>
            <p className='lead'>{data.category.name}</p>
            <div className='d-flex justify-content-between'>
            <p className='lead'> <span className='text-main'>{data.price}</span> EGP</p>
            <span>
              <i className="fa-solid fa-star rating-color"></i>{" "}
              {data.ratingsAverage}
            </span>
            </div>
          {Login?<button className="btn btndatails w-100 text-white" onClick={()=>{mutate(data._id)}}> + add to cart  </button>:""}
        </div>
        <Helmet>
  <title>{data.title}</title>
  <meta name="description" content="Helmet application" />
</Helmet>
    </div>
    
        }

        </>
  )
}

export function Error({error}) {
    return ( <h2 className="d-flex justify-content-center align-items-center">{error.message}</h2>);
  }

