import axios from 'axios';
import React from 'react'
import { useQuery } from 'react-query';
import Card from 'react-bootstrap/Card';
import Loading from '../Loading';
import { Helmet } from "react-helmet";
import { Error } from '../Products/Products';

export default function Categories() {
  function getcategory() {
    return axios.get('https://ecommerce.routemisr.com/api/v1/categories')
}

let {data,isLoading,isError,error} =useQuery('getcat',getcategory,{ 
    select: 
    (data) => data.data.data,
    }
);
  function getSubcategory() {
    return axios.get('https://ecommerce.routemisr.com/api/v1/subcategories')
}

let {data:subdata} =useQuery('getSubcat',getSubcategory,{
  select:(subdata)=>subdata.data.data.slice(1,11)
});

return (
  <>
  <Helmet>
  <title>Categories</title>
  <meta name="description" content="Helmet application" />
</Helmet>
  {isLoading ?
   (<Loading></Loading>) :
  isError ? 
  <Error key={error} error={error}></Error>: 
  <>
    <div className='row gy-5'>
      {data?.map((prod)=><div className="col-md-3" key={prod._id} id={prod._id}> 
      <Card style={{ width: '100%' }} className='catCard'>
      <Card.Img variant="top" src={prod.image} className='w-100' style={{height:'300px'}}/>
      <Card.Body>
        <Card.Title>{prod.name}</Card.Title>
      </Card.Body>
    </Card>
      </div>)
      }
    </div>
<div className="row g-4  my-3">
  <h2 className='text-center text-main'>Electronics subcategories</h2>
  {subdata?.map(ele=><div className="col-md-4  text-center">
    <div className=' border-1 border rounded-3 catCard border-bg-dark-subtle '>

    <h2 className='h4 my-3'>{ele.name}</h2>
    </div>

  </div>
  )
  }
</div>
      </>
}
      </>
  )
}
