import axios from 'axios';
import { useFormik } from 'formik'
import React, {  useState } from 'react'
import { Bars } from 'react-loader-spinner';
import { useNavigate } from 'react-router-dom';
import * as Yup from 'yup';

export default function Register() {
  const Navigate =useNavigate()
  const [laod,setlaoding]=useState(false);
  const [msg,setMsg]=useState('');

  
  async function getregister(values){
    try {
      setlaoding(true)
      const {data} = await axios.post('https://ecommerce.routemisr.com/api/v1/auth/signup',values);
      console.log(data);
      if (data.message === "success"){
        Navigate("/")
        setMsg("")
        setlaoding(false)
      }

    } catch (error) {
      setlaoding(true)
      setMsg(error.response.data.message)
      console.log(msg);
      setlaoding(false)
    }
  }
  const validationSchema = Yup.object({
    name:Yup.string().required("Name Is Required").min(2,"Short Min Is 2").max(10,"long Max is 10"),
    email:Yup.string().required("Email Is Required").email("Enter Valid eMail"),
    password:Yup.string().required("Password Is Required").min(5,"Short Min Is 5").matches(/^[A-Z][a-z0-9]{5,20}$/,"Invalid Password ex(A123abc)"),
    rePassword:Yup.string().required("rePassword Is Required").oneOf([Yup.ref('password')],"Must Be Combitable with password"),
    phone:Yup.string().required("Phone is Required").matches(/^(02)?(01)[0-25][0-9]{8}$/,"Phone Not Valid")
  })

  
  const formik=useFormik({
      initialValues:{
        name:"",
        email:'',
        password:'',
        rePassword:'',
        phone:''
      },
      validationSchema,
      onSubmit:getregister
    })
    
  return (<>
    <div>
      <form className='w-75 mx-auto' onSubmit={formik.handleSubmit}>
        <h3>Register Now:</h3>
        <label htmlFor="name">name:</label>
        <input type="text" id='name' className='form-control mb-3' value={formik.values.name} onChange={formik.handleChange} onBlur={formik.handleBlur}/>
        {formik.errors.name && formik.touched.name?<p className='alert alert-danger'>{formik.errors.name} </p>:"" }

        <label htmlFor="email">email:</label>
        <input type="email" id='email' className='form-control mb-3' value={formik.values.email} onChange={formik.handleChange} onBlur={formik.handleBlur}/>
        {formik.errors.email && formik.touched.email?<p className='alert alert-danger'>{formik.errors.email} </p>:"" }


        <label htmlFor="password">password:</label>
        <input type="password" id='password' className='form-control mb-3' value={formik.values.password} onChange={formik.handleChange} onBlur={formik.handleBlur}/>
        {formik.errors.password && formik.touched.password?<p className='alert alert-danger'>{formik.errors.password} </p>:"" }

        <label htmlFor="rePassword">rePassword:</label>
        <input type="password" id='rePassword' className='form-control mb-3' value={formik.values.rePassword} onChange={formik.handleChange} onBlur={formik.handleBlur}/>
        {formik.errors.rePassword && formik.touched.rePassword?<p className='alert alert-danger'>{formik.errors.rePassword} </p>:"" }

        <label htmlFor="phone">phone:</label>
        <input type="rel" id='phone' className='form-control mb-3' value={formik.values.phone} onChange={formik.handleChange} onBlur={formik.handleBlur}/>
        {formik.errors.phone && formik.touched.phone?<p className='alert alert-danger'>{formik.errors.phone} </p>:"" }
        
        {msg? <p className=' alert alert-danger'>{msg}</p> :""}
        <button disabled={!(formik.dirty&& formik.isValid)} type='submit' className='btn  regbtn text-white ms-auto d-block' >{laod?<Bars
  height="24"
  width="65"
  color="white"
  ariaLabel="bars-loading"
  wrapperStyle={{}}
  wrapperClass=""
  visible={true}
  />:"Register"}</button>
      </form>
    </div>
  </>
  )
}
