import axios from "axios";
import toast from "react-hot-toast";
import { useMutation, useQuery, useQueryClient } from "react-query";

const Baseurl='https://ecommerce.routemisr.com/api/v1'



export function useWishlist(productId){
    const token =localStorage.getItem('UserToken')
    return axios.post(`${Baseurl}/wishlist`,{productId},{
        headers:{
            token
        }
    }
    )
}


export function useWishlistMutation(fn){
    const QueryClient =new useQueryClient()
        return useMutation(fn,{
            onSuccess:(data)=>{
                toast.success(data?.data?.message);
                QueryClient.invalidateQueries('wishlist')
            },
            onError:(data)=>{
                toast.error(data?.message);
            }
        })
    }

export function useGetWishlist(){
    const token =localStorage.getItem('UserToken')
    return axios.get(`${Baseurl}/wishlist`,{
        headers:{
            token
        }
    }
    )
}


    export function useWishlisttoget(key,fn){
        return  useQuery(
              key,
              fn,
              { select: 
              (data) => data.data,
              })
      }

      export function usedeletewishlist(productId){
    const token =localStorage.getItem('UserToken')
        return axios.delete(`${Baseurl}/wishlist/${productId}`,{
            headers:{
                token
            }
        }
        )
    }