import React from 'react'
import { useGetWishlist, useWishlistMutation, useWishlisttoget, usedeletewishlist } from '../Usewishlist';
import Loading from '../Loading';
import { Error } from '../Home/Home';
import logo from '../../assets/preview.png'
import { useCart, useCartMutation } from '../UseCart';


export default function Wishlist() {

  let { data, isError, isLoading, error } = useWishlisttoget("wishlist", useGetWishlist);


console.log(data?.count);

  return (
    <div className="container bg-light">
          {
            <div className="row align-items-center p-5 g-5 ">
              {isLoading ? (
                <Loading></Loading>
              ) : isError ? (
                <Error key={error} error={error}></Error>
              ) : data?.count ? (
                <>
                  <div className="d-flex justify-content-between mb-4 ">
                    <h2 className="fw-bold ">My wish List</h2>    
                  </div>
                  {data?.data?.map((prod) => (
                    <Wishlistitem key={prod._id} prod={prod}></Wishlistitem>
                  ))}
                </>
              ) : (
                <div className=" row text-center">
                  <div className="col-md-6"></div>
                  <h2>wish List Is Empty</h2>
                  <img src={logo} alt=""  className="w-100" style={{maxHeight:"500px"}}/>
                </div>
              )}
            </div>
          }
        </div>
  )
}


export function Wishlistitem({ prod }) {
  let { mutate:deletemutate } = useWishlistMutation(usedeletewishlist);
  let { mutate } = useCartMutation(useCart)
  return (
    <>
      <div className="col-md-2">
        <img src={prod.imageCover} alt="" className="w-100" />
      </div>
      <div className="col-md-8">
        <h3>{prod.title}</h3>
        <p>
          Price : <span className="text-main">{prod.price}</span>
        </p>
        <span
          className="cursor-pointer text-danger "
          onClick={() => {
            deletemutate(prod._id);
          }}
        >
          {" "}
          <i className="fa-regular fa-trash-can "></i> Remove
        </span>
      </div>
      <div className="col-md-2 d-flex align-items-center justify-content-center gap-3">
        <button
          className="btnbrdr btn"
          onClick={()=>{mutate(prod._id)}}
        >
          Add to Card
        </button>
      </div>
    </>
  );
}