import axios from 'axios';
import React from 'react'
import { useQuery } from 'react-query';
import Slider from 'react-slick';
import slider1 from "../assets/slider-image-1.jpeg";
import slider2 from "../assets/slider-image-2.jpeg";
import slider3 from "../assets/slider-image-3.jpeg";

export default function CategorySlider() {
    function getcategory() {
        return axios.get('https://ecommerce.routemisr.com/api/v1/categories')
    }

    let {data} =useQuery('getcat',getcategory,{ 
        select: 
        (data) => data.data.data,

        }
    );

    var settings = {
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 7,
        slidesToScroll: 1,
        autoplay:true,
        responsive: [
          {
            breakpoint: 800,
            settings: {
              slidesToShow: 5,
              slidesToScroll: 1,
              infinite: true,
              dots: true
            }
          },
          {
            breakpoint: 600,
            settings: {
              slidesToShow: 2,
              slidesToScroll: 1,
              infinite: true,
              dots: true
            }
          },{
            breakpoint: 400,
            settings: {
              slidesToShow: 1,
              slidesToScroll: 1,
              infinite: true,
              dots: true
            }
          },
        ]
      };

  return (
    <Slider {...settings}>
    {data?.map((ele)=> 
    <div key={ele._id} className='position-relative slider overflow-hidden'>
        <img  src={ele.image} alt="" height={200} className='w-100' />
        <div className="slide d-flex justify-content-center align-items-center position-absolute top-0 bottom-0 w-100 ">
            <p>{ele.name}</p>
        </div>
    </div>
    )}

    </Slider>
    );
}

export function SimpleSlider() {
    const settings = {
      dots: true,
      infinite: true,
      speed: 500,
      slidesToShow: 1,
      slidesToScroll: 1,
    };
    return (
      <div className="slider-container">
        <Slider {...settings}>
          <div>
          <img  src={slider1} alt=""  className='w-100' style={{maxHeight:'400px'}}/>
          </div>
          <div>
          <img  src={slider2} alt=""  className='w-100' style={{maxHeight:'400px'}}/>
          </div>
          <div>
          <img  src={slider3} alt=""  className='w-100' style={{maxHeight:'400px'}} />
          </div>
        </Slider>
      </div>
    );
  }
  