import { useFormik } from 'formik'
import React, { useState } from 'react'
import * as Yup from 'yup';
import { useNavigate } from 'react-router-dom'
import { Bars } from 'react-loader-spinner';
import axios from 'axios';


export default function Forgetpass() {
  const [msg,setMsg]=useState('');
  const [laod,setlaoding]=useState(false);
  const navigate =useNavigate('')

  async function getCode(value){
    try {
      setlaoding(true)
      const {data} = await axios.post('https://ecommerce.routemisr.com/api/v1/auth/verifyResetCode',value);
      console.log(data);
      if (data.status === "Success"){
        localStorage.setItem("Code",value)
        navigate("/RessetPass")
        setMsg("")
        setlaoding(false)
      }
    } catch (error) {
        setlaoding(true)
        setMsg(error.response.data.message)
        setlaoding(false)  
  }
  }


    const validationSchema = Yup.object({
        resetCode:Yup.string().required("Code is Required").matches(/[0-9]{6}/,"Code Not Valid")
    })

    const formik=useFormik({
        initialValues:{
            resetCode:'',
        },
        validationSchema,
        onSubmit:getCode
      })
  return (
    <div>
        <form className='w-75 mx-auto' onSubmit={formik.handleSubmit}>
        <p className=' alert alert-success'>Reset code sent to your email</p>
        <br />
        <h2 className='fw-bold'> reset your account password</h2>

        <label htmlFor="resetCode">resetCode:</label>
        <input type="rel" id='resetCode' className='form-control mb-3' value={formik.values.resetCode} onChange={formik.handleChange} onBlur={formik.handleBlur}/>
        {formik.errors.resetCode && formik.touched.resetCode?<p className='alert alert-danger'>{formik.errors.resetCode} </p>:"" }

        {msg? <p className=' alert alert-danger'>{msg}</p> :""}
        <div className='d-flex justify-content-between'>

        <button disabled={!(formik.dirty&& formik.isValid)} type='submit' className='btn  regbtn text-white ms-auto d-block' >{laod?<Bars
  height="24"
  width="65"
  color="white"
  ariaLabel="bars-loading"
  wrapperStyle={{}}
  wrapperClass=""
  visible={true}
  />:"Verify"}</button>
  </div>
      </form>
    </div>
  )
}
