import axios from "axios";
import toast from "react-hot-toast";
import { useMutation, useQuery, useQueryClient } from "react-query";

const Baseurl = "https://ecommerce.routemisr.com/api/v1";
const token = localStorage.getItem("UserToken");
export function useCart(productId) {
  const token = localStorage.getItem("UserToken");
  return axios.post(
    `${Baseurl}/cart`,
    { productId },
    {
      headers: {
        token,
      },
    }
  );
}

export function checkoutAPI({ id, shippingAddress }) {
  const token = localStorage.getItem("UserToken");
  return axios.post(
    `https://ecommerce.routemisr.com/api/v1/orders/checkout-session/${id}?url=https://sha3ban10.github.io/Ecommerce/`,
    { shippingAddress },
    {
      headers: {
        token,
      },
    }
  );
}

export function usegetCard() {
  const token = localStorage.getItem("UserToken");
  return axios.get(`${Baseurl}/cart`, {
    headers: {
      token,
    },
  });
}
export function usedeleteCard(productId) {
  const token = localStorage.getItem("UserToken");
  return axios.delete(`${Baseurl}/cart/${productId}`, {
    headers: {
      token,
    },
  });
}

export function usedeleteallCard() {
  const token = localStorage.getItem("UserToken");
  return axios.delete(`${Baseurl}/cart`, {
    headers: {
      token,
    },
  });
}

export function useUpdateCard({ productId, count }) {
  const token = localStorage.getItem("UserToken");
  return axios.put(
    `${Baseurl}/cart/${productId}`,
    { count },
    {
      headers: {
        token,
      },
    }
  );
}

export function useCartMutation(fn) {
  const QueryClient = new useQueryClient();
  return useMutation(fn, {
    onSuccess: (data) => {
      toast.success(data?.data?.message);
      QueryClient.invalidateQueries("Getcard");
      if (data?.data?.session?.url) {
        window.location.href = data?.data?.session?.url;
      }
    },
    onError: (data) => {
      toast.error(data?.message);
    },
  });
}

export function useCardtoGet(key, fn) {
  return useQuery(key, fn, { select: (data) => data.data });
}
