import React, { useEffect, useState } from "react";
import Loading from "../Loading";
import { FeturedProducts, useProduct } from "../../useProduct";
import { Product } from "../Product";

import blog1 from "../../assets/blog-img-1.jpeg";
import blog2 from "../../assets/blog-img-2.jpeg";
import CategorySlider, { SimpleSlider } from "../CategorySlider";
import { TextField } from "@mui/material";
import { Helmet } from "react-helmet";

export default function Home() {
  let { data, error, isError, isLoading } = useProduct(
    "products",
    FeturedProducts
  );
  // var settings = {
  //   dots: true,
  //   infinite: true,
  //   speed: 500,
  //   slidesToShow: 1,
  //   slidesToScroll: 1
   
  // };
  const [searcheditems, setsearcheditems] = useState([]);

  function SearchForProduct(e) {
    const search = e.target.value;
    const Arr = data?.filter((ele) =>
      ele.title.toLowerCase().trim().includes(search.toLowerCase().trim())
    );
    setsearcheditems(Arr);
  }



  return (
    <>
      <Helmet>
        <title>Home</title>
        <meta name="description" content="Helmet application" />
      </Helmet>

      <div className="row gx-0 ">
        <div className="col-md-8 ">
          <SimpleSlider></SimpleSlider>
        </div>
        <div className=" col-md-4">
          <img  src={blog1} alt=""  className='w-100' style={{maxHeight:'200px'}} />
          <img  src={blog2} alt=""  className='w-100' style={{maxHeight:'200px'}} />
        </div>
      </div>


      <CategorySlider></CategorySlider>

      <TextField
        id="outlined-basic"
        label="Search"
        variant="outlined"
        className="form-control mt-5"
        onChange={SearchForProduct}
      />

      <div className="row gy-4 my-4">
        {isLoading ? (
          <Loading></Loading>
        ) : isError ? (
          <Error key={error} error={error}></Error>
        ) : searcheditems.length ? (
          searcheditems.map((prod) => (
            <Product key={prod._id} prod={prod}></Product>
          ))
        ) : (
          data?.map((prod) => <Product key={prod._id} prod={prod}></Product>)
        )}
      </div>
    </>
  );
}

export function Error({ error }) {
  return (
    <h2 className="d-flex justify-content-center align-items-center">
      {error.message}
    </h2>
  );
}
