import React, { useContext } from "react";
import { Usercontext } from "../../UserContext";
import {
  checkoutAPI,
  useCardtoGet,
  useCartMutation,
  useUpdateCard,
  usedeleteCard,
  usedeleteallCard,
  usegetCard,
} from "../UseCart";
import Loading from "../Loading";
import { useNavigate } from "react-router-dom";
import Button from "react-bootstrap/Button";
import Modal from "react-bootstrap/Modal";
import { useState } from "react";
import { TextField } from "@mui/material";
import logo from '../../assets/preview.png'
import { Helmet } from "react-helmet";

export default function Cart() {

  const [details,setdetails]= useState('')
  const [phone,setphone]= useState('')
  const [city,setcity]= useState('')

  const [show, setShow] = useState(false);
  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  const navigate = new useNavigate();

  function gethome() {
    setOpen(false);

    navigate("/home");
  }

  const { Open, setOpen } = useContext(Usercontext);
  
  let { data, isError, isLoading, error } = useCardtoGet("Getcard", usegetCard);
  
  let { data:checkoutdata, mutate:checkoutmutate } = useCartMutation(checkoutAPI);
  let { mutate } = useCartMutation(usedeleteallCard);


  let shippingAddress={
    details,phone,city
  }
  let id =data?.data?._id;


function check(e){
  e.preventDefault()
  checkoutmutate({id,shippingAddress})
}

function deleteall(){
  mutate()
  navigate("/home")
}
  return (
    <>
    <Helmet>
  <title>Cart</title>
  <meta name="description" content="Helmet application" />
</Helmet>
      <aside
        className="right"
        style={
          Open
            ? { right: "0", transition: "right 0.5s" }
            : { right: "-100%", transition: "right 0.5s" }
        }
      >
        <i
          className="fa-solid fa-close p-3 fa-2x cursorPointer"
          onClick={gethome}
        ></i>
        <div className="container bg-white">
          {
            <div className="row align-items-center p-5 justify-content-center ">
              {(data?.data?._id  )? 
              (isLoading ? (
                <Loading></Loading>
              ) : isError ? (
                <div className=" row text-center">
                  <div className="col-md-6"></div>
                  <h2>Cart Is Empty</h2>
                  <img src={logo} alt=""  className="w-100" style={{maxHeight:"500px"}}/>
                </div>
              ) : data?.numOfCartItems ? (
                <>
                  <div className="d-flex justify-content-between mb-4 ">
                    <h2 className="fw-bold ">Shop Cart :</h2>
                    <Button variant="success" onClick={handleShow}>
                      Check out
                    </Button>
                    
                  </div>
                  <div className="d-flex justify-content-between">
                    <p className="h4">
                      Total Cart price :{" "}
                      <span className="text-main">
                        {data?.data?.totalCartPrice}
                      </span>
                    </p>
                    <p className="h4">
                      Number of Cart Items :{" "}
                      <span className="text-main">{data?.numOfCartItems}</span>
                    </p>
                  </div>
                  {data?.data?.products.map((prod) => (
                    <CartItem key={prod._id} prod={prod}></CartItem>
                  ))}
               <Button variant="success" onClick={deleteall} className="w-25"> Clear Cart</Button>
                </>
              ) : (
                <div className=" row text-center">
                  <div className="col-md-6"></div>
                  <h2>Cart Is Empty</h2>
                  <img src={logo} alt=""  className="w-100" style={{maxHeight:"500px"}}/>
                </div>)
              )
            :
              <div className=" row text-center">
              <div className="col-md-6"></div>
              <h2>Cart Is Empty</h2>
              <img src={logo} alt=""  className="w-100" style={{maxHeight:"500px"}}/>
            </div>
              }
            </div>
          }
        </div>

        <Modal show={show} onHide={handleClose}>
        <Modal.Header closeButton className="bg-success-subtle">
          <Modal.Title className="fw-bolder">Check out</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <form action="">
            <TextField id="outlined-basic" label="details" variant="outlined" className='form-control mt-2' onChange={(e)=>setdetails(e.target.value)} />
            <TextField id="outlined-basic" label="phone" variant="outlined" className='form-control mt-2' onChange={(e)=>setphone(e.target.value)} />
            <TextField id="outlined-basic" label="city" variant="outlined" className='form-control my-2' onChange={(e)=>setcity(e.target.value)} />
            <Button variant="success" type="submit" onClick={check}>Check out</Button>
          </form>
        </Modal.Body>
      </Modal>
      </aside>
    </>
  );
}


export function CartItem({ prod }) {
  let { mutate } = useCartMutation(usedeleteCard);
  let { mutate: MutateUpdate, data: dataUpdate } =
    useCartMutation(useUpdateCard);
  console.log(dataUpdate);
  return (
    <>
      <div className="col-md-2">
        <img src={prod.product.imageCover} alt="" className="w-100" />
      </div>
      <div className="col-md-8">
        <p>{prod.product.title}</p>
        <p>
          Price : <span className="text-main">{prod.price}</span>
        </p>
        <span
          className="cursor-pointer text-danger "
          onClick={() => {
            mutate(prod.product._id);
          }}
        >
          {" "}
          <i className="fa-regular fa-trash-can "></i> Remove
        </span>
      </div>
      <div className="col-md-2 d-flex align-items-center justify-content-center gap-3">
        <button
          className="btnbrdr btn"
          onClick={() =>
            MutateUpdate({ productId: prod.product._id, count: prod.count + 1 })
          }
        >
          +
        </button>
        <span>{prod.count}</span>
        <button
          className="btnbrdr btn"
          onClick={() =>prod.count==1?mutate(prod.product._id):
            MutateUpdate({ productId: prod.product._id, count:(prod.count)>0?  prod.count - 1:prod.count })
          }
        >
          -
        </button>
      </div>
    </>
  );
}

export function Error({ error }) {
  return (
    <h2 className="d-flex justify-content-center align-items-center">
      {error.message}
    </h2>
  );
}
