import React, { useContext, useEffect } from "react";
import {
  RouterProvider,
  createBrowserRouter,
  createHashRouter,
} from "react-router-dom";
import Layout from "./components/Layout";
import Login from "./components/Login/Login";
import Register from "./components/Register/Register";
import Products from "./components/Products/Products";
import Categories from "./components/Categories/Categories";
import Brands from "./components/Brands/Brands";
import NotFound from "./components/NotFound";
import Home from "./components/Home/Home";
import Cart from "./components/Cart/Cart";
import { Usercontext } from "./UserContext";
import ProtectedRoute from "./components/ProtectedRoute";
import ProductDetails from "./components/ProductDetails/ProductDetails";
import Forgetpass from "./components/Forgetpass/Forgetpass";
import Code from "./components/Forgetpass/Code";
import RessetPass from "./components/Forgetpass/RessetPass";
import ProtectedCode from "./components/ProtectedCode";
import Orders from "./components/Orders/Orders";
import Wishlist from "./components/Wishlist/Wishlist";

export default function App() {
  let { setUser, setLogin } = useContext(Usercontext);

  useEffect(() => {
    if (localStorage.getItem("UserToken")) {
      setUser(localStorage.getItem("UserToken"));
      setLogin(localStorage.getItem("UserName"));
    }
  }, []);

  const routes = createHashRouter([
    {
      path: "",
      element: <Layout></Layout>,
      children: [
        { index: true, element: <Home></Home> },
        { path: "Login", element: <Login></Login> },
        { path: "register", element: <Register></Register> },
        { path: "Forgetpass", element: <Forgetpass></Forgetpass> },
        { path: "Code", element: <Code></Code> },
        {
          path: "RessetPass",
          element: (
            <ProtectedCode>
              <RessetPass></RessetPass>
            </ProtectedCode>
          ),
        },
        {
          path: "cart",
          element: (
            <ProtectedRoute>
              <Cart></Cart>
            </ProtectedRoute>
          ),
        },
        {
          path: "Wishlist",
          element: (
            <ProtectedRoute>
              <Wishlist></Wishlist>
            </ProtectedRoute>
          ),
        },
        { path: "home", element: <Home></Home> },
        {
          path: "Products",
          element: (
            <ProtectedRoute>
              <Products></Products>
            </ProtectedRoute>
          ),
        },
        {
          path: "ProductDetails/:id",
          element: <ProductDetails></ProductDetails>,
        },
        {
          path: "categories",
          element: (
            <ProtectedRoute>
              <Categories></Categories>
            </ProtectedRoute>
          ),
        },
        {
          path: "/allorders",
          element: (
            <ProtectedRoute>
              <Orders></Orders>
            </ProtectedRoute>
          ),
        },
        {
          path: "brands",
          element: (
            <ProtectedRoute>
              <Brands></Brands>
            </ProtectedRoute>
          ),
        },
        { path: "*", element: <NotFound></NotFound> },
      ],
    },
  ]);

  return <RouterProvider router={routes}></RouterProvider>;
}
