import React, { useContext, useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { useCart, useCartMutation } from "./UseCart";
import {  useGetWishlist, useWishlist, useWishlistMutation, useWishlisttoget, usedeletewishlist } from "./Usewishlist";
import { Usercontext } from "../UserContext";

export function Product({ prod }) {
  const {Login}= useContext(Usercontext)

  const [heart,setheart] = useState(false)
  let { mutate } = useCartMutation(useCart)
  let { mutate:wishlistmutate } = useWishlistMutation(useWishlist)
  let { mutate:deletemutate } = useWishlistMutation(usedeletewishlist);
  let { data } = useWishlisttoget("wishlist", useGetWishlist);

  useEffect(() => {
    if(data?.data?.find((ele)=>(ele._id===prod._id)))
    {
      setheart(true)
    }
    }, [data?.data])
  

  function postWIshlist(){
    setheart((prev) => !prev)
    if(!heart){
      wishlistmutate(prod._id)
    }
    else{
      deletemutate(prod._id)
    }
  }
    return (
      <>
        <div className="col-md-2 cursor-pointer product ">
          {Login?<i className="fa-solid fa-heart fs-4 overflow-hidden" style={heart?{color:"red"}:{color:"gray"}} onClick={postWIshlist}></i>:""}
          <Link to={`/ProductDetails/${prod._id}`}>
          <img src={prod.imageCover} alt="product logo" className="w-100 " />

          <p className="text-main pt-1">{prod.category.name}</p>
          <h2 className="h5">{prod.title}</h2>
          <div className="d-flex justify-content-between">
            <span>{prod.price} EGP</span>
            <span>
              <i className="fa-solid fa-star rating-color"></i>{" "}
              {prod.ratingsAverage}
            </span>
          </div>
          </Link>
          {Login?<button className="btn my-2" onClick={()=>{mutate(prod._id)}}>add to cart  </button>:""}
        </div>
      </>
    );
  }
  